/** @jsxImportSource @emotion/react */
import { css } from "@emotion/react";

function Footer() {
  const outerBox = css`
    text-align: center;
    width: 100%;
    height: 18vh;
  `;
  const container = css`
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100%;
    background-color: #28323c;
    color: #67707e;
    line-height: 0;
    font-size: 12px;
    p {
      height: 3vh;
      display: block;
      margin: 0;
    }
  `;

  return (
    <div css={outerBox}>
      <div css={container}>
        <p> / </p>
        <p>   /  </p>
        <p> : 010-9400-0438</p>
        <p>@ BP CORP</p>
      </div>
    </div>
  );
}

export default Footer;
