/** @jsxImportSource @emotion/react */
import { css } from "@emotion/react";

import { connect } from "react-redux";
import { userInfo } from "../../modules/signUp";

import Alert from "@mui/material/Alert";

const inputBox = css`
  position: relative;
  margin: 5px 0;
  width: 70vw;
`;

const inputChild = css`
  background: transparent;
  border: none;
  border-bottom: solid 1px #ccc;
  padding: 20px 0px 5px 0px;
  font-size: 14pt;
  width: 100%;
`;

const infoBox = css`
  display: flex;
`;

function SignUpId({ info, setInfo }) {
  // id  
  const idRegExp = /^[a-z][0-9a-z]{6,19}$/g;

  //  
  const typeId = (e) => {
    const idInput = e.target.value;
    setInfo((info) => {
      return { ...info, id: idInput };
    });

    idRegExp.test(idInput);

    //  ?
    if (idRegExp.test(idInput) && idInput.length >= 8 && idInput.length <= 20) {
      setInfo((info) => {
        return { ...info, idSuccess: true };
      });
    } else {
      setInfo((info) => {
        return { ...info, idSuccess: false };
      });
    }
  };

  return (
    <div css={inputBox}>
      <div>
        <input
          type="text"
          id="userId"
          css={inputChild}
          placeholder=""
          autoComplete="off"
          value={info.id}
          required
          onChange={typeId}
        />

        <label htmlFor="userId">
          <div css={infoBox}>
            <div></div>
          </div>
        </label>
      </div>

      {/*   */}
      {(idRegExp.test(info.id) &&
        info.id.length >= 8 &&
        info.id.length <= 20) ||
      info.id.length === 0 ? (
        info.id.length === 0 ? null : (
          <Alert
            sx={{
              hieght: "10%",
              fontSize: "12px",
              paddingTop: "0",
              paddingBottom: "0",
              display: "flex",
              justifyContent: "center",
            }}
            variant="outlined"
            severity="success"
          >
             .
          </Alert>
        )
      ) : (
        <div>
          <Alert
            sx={{
              hieght: "10%",
              fontSize: "12px",
              paddingTop: "0",
              paddingBottom: "0",
              display: "flex",
              justifyContent: "center",
            }}
            variant="outlined"
            severity="error"
          >
             .
          </Alert>
        </div>
      )}
    </div>
  );
}

const mapStateToProps = ({ signUp }) => {
  return { signUp };
};

const mapDispatchToProps = (dispatch) => {
  return {
    idTyping(userId) {
      dispatch(userInfo.idTyping(userId));
    },
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(SignUpId);
