/** @jsxImportSource @emotion/react */
import { css } from "@emotion/react";
import { gsap } from "gsap/all";
import { useEffect, useRef } from "react";
import img2 from "../../style/section2.png";

const outerBox = css`
  display: flex;
  flex-direction: column;
  justify-content: center;
  text-align: right;
  background: url(${img2});
  background-size: cover;
  width: 100vw;
  height: 100vh;
  opacity: 0.2;
`;

function HomeSection2() {
  const backGround = useRef();
  useEffect(() => {
    if (!backGround) return;

    gsap.to(".backTrigger", {
      opacity: 1,
      scrollTrigger: {
        trigger: ".backTrigger",
        start: "top 90%",
        endTrigger: ".backTrigger",
        end: () => "+=400",
        scrub: 1,
        // markers: true,
        // id: "",
      },
    });

    gsap.to(".yellowText, .greenText", {
      opacity: 1,
      scrollTrigger: {
        trigger: ".backTrigger",
        start: "400 90%",
        endTrigger: ".backTrigger",
        end: () => "+=200",
        scrub: 1,
        // markers: true,
        id: "",
      },
    });

    gsap.to(".innerText", {
      opacity: 1,
      x: 0,
      scrollTrigger: {
        trigger: ".backTrigger",
        start: "400 90%",
        endTrigger: ".backTrigger",
        end: () => "+=200",
        scrub: 1,
        // markers: true,
        id: "innertext",
      },
    });
  }, [backGround]);
  return (
    <div css={outerBox} ref={backGround} className="backTrigger">
      <p
        style={{
          marginRight: "5%",
          color: "white",
          fontSize: "2rem",
          fontWeight: "900",
          marginBottom: "0",
        }}
      >
        <span css={{ color: "#CCFF3C", opacity: 0 }} className="yellowText">
          
        </span>
         
      </p>

      <p
        css={{
          marginRight: "5%",
          color: "#00FF66",
          fontSize: "2rem",
          fontWeight: "900",
          opacity: 0,
        }}
        className="greenText"
      >
        
      </p>
      <p
        css={{
          marginRight: "5%",
          marginLeft: "5%",
          color: "white",
          textAlign: "justify",
          transform: "translateX(100px)",
          opacity: 0,
        }}
        className="innerText"
      >
          3,500   ,   
          .     
             .
      </p>
    </div>
  );
}

export default HomeSection2;
