/** @jsxImportSource @emotion/react */
import { css } from "@emotion/react";

import { connect } from "react-redux";
import Alert from "@mui/material/Alert";

const inputBox = css`
  position: relative;
  margin: 5px 0;
  width: 70vw;
`;

const inputChild = css`
  background: transparent;
  border: none;
  border-bottom: solid 1px #ccc;
  padding: 20px 0px 5px 0px;
  font-size: 14pt;
  width: 100%;
`;

function SignUpName({ info, setInfo }) {
  // userName  
  const nameRegExp = /[!@#%&;'":<>`~.*+?^${}()|[\]\\a-zA-Z0-9]/g;

  //  
  const typeName = (e) => {
    const nameInput = e.target.value;

    setInfo((info) => {
      return { ...info, userName: nameInput };
    });

    //  ?
    if (!info.userName.match(nameRegExp) || info.userName.length === 0) {
      setInfo((info) => {
        return { ...info, userNameSuccess: true };
      });
    } else {
      setInfo((info) => {
        return { ...info, userNameSuccess: false };
      });
    }
  };

  return (
    <div css={inputBox}>
      <div>
        <input
          type="text"
          id="userName"
          placeholder=""
          autoComplete="off"
          required
          css={inputChild}
          onChange={typeName}
          value={info.userName}
        />
        <label htmlFor="userName"></label>
      </div>

      {!info.userName.match(nameRegExp) || info.userName.length === 0 ? (
        info.userName.length === 0 ? null : (
          <Alert
            sx={{
              hieght: "10%",
              fontSize: "12px",
              paddingTop: "0",
              paddingBottom: "0",
              display: "flex",
              justifyContent: "center",
            }}
            variant="outlined"
            severity="success"
          >
            .
          </Alert>
        )
      ) : (
        <Alert
          sx={{
            hieght: "10%",
            fontSize: "12px",
            paddingTop: "0",
            paddingBottom: "0",
            display: "flex",
            justifyContent: "center",
          }}
          variant="outlined"
          severity="error"
        >
           .
        </Alert>
      )}
    </div>
  );
}

const mapStateToProps = ({ signUp }) => {
  return { signUp };
};

const mapDispatchToProps = (dispatch) => {
  return {};
};

export default connect(mapStateToProps, mapDispatchToProps)(SignUpName);
