import Alert from "@mui/material/Alert";

function NextPwdCondition({ pwdRegExp, info }) {
  return (
    <>
      {(pwdRegExp.test(info.next) && info.next !== info.current) ||
      info.next.length === 0 ? (
        info.next.length === 0 ? null : (
          <Alert
            sx={{
              hieght: "10%",
              fontSize: "12px",
              paddingTop: "0",
              paddingBottom: "0",
              display: "flex",
              justifyContent: "center",
            }}
            variant="outlined"
            severity="success"
          >
             .
          </Alert>
        )
      ) : info.next === info.current ? (
        <Alert
          sx={{
            hieght: "10%",
            fontSize: "12px",
            paddingTop: "0",
            paddingBottom: "0",
            display: "flex",
            justifyContent: "center",
          }}
          variant="outlined"
          severity="error"
        >
            .
        </Alert>
      ) : (
        <Alert
          sx={{
            hieght: "10%",
            fontSize: "12px",
            paddingTop: "0",
            paddingBottom: "0",
            display: "flex",
            justifyContent: "center",
          }}
          variant="outlined"
          severity="error"
        >
           .
        </Alert>
      )}
    </>
  );
}

export default NextPwdCondition;
