import Alert from "@mui/material/Alert";

function ConfirmCondition({ info }) {
  return (
    <>
      {info.next !== info.confirmPwd && info.confirmPwd ? (
        <Alert
          sx={{
            hieght: "10%",
            fontSize: "12px",
            paddingTop: "0",
            paddingBottom: "0",
            display: "flex",
            justifyContent: "center",
          }}
          variant="outlined"
          severity="error"
        >
           .
        </Alert>
      ) : info.confirmPwd.length === 0 ? null : (
        <Alert
          sx={{
            hieght: "10%",
            fontSize: "12px",
            paddingTop: "0",
            paddingBottom: "0",
            display: "flex",
            justifyContent: "center",
          }}
          variant="outlined"
          severity="success"
        >
           .
        </Alert>
      )}
    </>
  );
}

export default ConfirmCondition;
