/** @jsxImportSource @emotion/react */
import { css } from "@emotion/react";

import * as React from "react";
import Box from "@mui/material/Box";
import Drawer from "@mui/material/Drawer";
import Button from "@mui/material/Button";
import List from "@mui/material/List";
import Divider from "@mui/material/Divider";
import ListItem from "@mui/material/ListItem";
import ListItemButton from "@mui/material/ListItemButton";
import ListItemIcon from "@mui/material/ListItemIcon";
import ListItemText from "@mui/material/ListItemText";
import MenuIcon from "@mui/icons-material/Menu";
import logo from "../style/logo.png";

import LogoutIcon from "@mui/icons-material/Logout";
import PermIdentityIcon from "@mui/icons-material/PermIdentity";
import FmdGoodIcon from "@mui/icons-material/FmdGood";
import LoginIcon from "@mui/icons-material/Login";
import AssignmentIcon from "@mui/icons-material/Assignment";

import { useNavigate } from "react-router-dom";
import { useDispatch } from "react-redux";
import { logOut } from "../modules/userLogin";
import { useEffect } from "react";
import { pointer, pointers } from "d3";

const divStyle = css`
  display: flex;
  justify-content: space-between;
  position: sticky;
  width: 100%;
  height: 8vh;
  font-size: 1rem;
`;
export default function Nav() {
  const [state, setState] = React.useState({
    right: false,
  });

  const navigation = useNavigate();
  const dispatch = useDispatch();
  const iconRef = React.useRef(null);

  const toggleDrawer = (anchor, open) => (event) => {
    if (
      event.type === "keydown" &&
      (event.key === "Tab" || event.key === "Shift")
    ) {
      return;
    }
    setState({ ...state, [anchor]: open });
  };

  useEffect(() => {
    // localStorage   ()
    const objString = localStorage.getItem("login-token");
    // null 
    if (objString !== null && objString !== undefined) {
      //   
      const obj = JSON.parse(objString);
      //   localStorage expire  
      if (Date.now() > obj.expire) {
        //   item 
        localStorage.removeItem("login-token");
        alert(" .");
        navigation("/bp/login");
      }
    }
  });

  const navItemClick = (e) => {
    switch (e.target.getAttribute("value") || e.target.textContent) {
      case "":
        navigation("/bp/login");
        return;
      case "":
        navigation("/bp/terms");
        return;
      case "":
        dispatch(logOut());
        navigation("/bp");
        return;
      case "":
        navigation("/bp/mypage");
        return;
      case "":
        navigation("/bp/map");
        return;

      default:
        return;
    }
  };

  const list = (anchor) => (
    <Box
      sx={{ width: anchor === "top" || anchor === "bottom" ? "auto" : 250 }}
      role="presentation"
      onClick={toggleDrawer(anchor, false)}
      onKeyDown={toggleDrawer(anchor, false)}
    >
      <List>
        {(localStorage.getItem("login-token")
          ? //   
            ["", "", ""]
          : ["", ""]
        ).map((text, index) => (
          <ListItem
            key={text}
            value={text}
            disablePadding
            onClick={navItemClick}
          >
            <ListItemButton value={text}>
              <ListItemIcon value={text}>
                {text === "" ? (
                  <LogoutIcon value={text} />
                ) : text === "" ? (
                  <PermIdentityIcon value={text} />
                ) : text === "" ? (
                  <FmdGoodIcon value={text} />
                ) : text === "" ? (
                  <LoginIcon value={text} />
                ) : text === "" ? (
                  <AssignmentIcon value={text} />
                ) : null}
              </ListItemIcon>
              <ListItemText primary={text} />
            </ListItemButton>
          </ListItem>
        ))}
      </List>
      <Divider />
    </Box>
  );

  const goToHome = () => {
    navigation("/bp");
  };

  return (
    <div css={divStyle}>
      <img
        src={logo}
        css={{
          height: "5.5vh",
          width: "11vh",
          marginLeft: "1.5vh",
          marginTop: "1.5vh",
          cursor: "pointer",
        }}
        alt="Brolly Project"
        onClick={goToHome}
      />
      <Button onClick={toggleDrawer("right", true)} height="30">
        <MenuIcon sx={{ fontSize: "35px", color: "black" }} />
      </Button>
      <Drawer
        anchor={"right"}
        open={state["right"]}
        onClose={toggleDrawer("right", false)}
      >
        {list("right")}
      </Drawer>
    </div>
  );
}
