import { useState } from "react";
import { connect } from "react-redux";
import { HistogramUseage } from "./HistogramUseage";
import { useEffect } from "react";
import { useSelector } from "react-redux";
import { useLocation } from "react-router-dom";
import { Histogram } from "./Histogram";

const BUTTONS_HEIGHT = 50;

const HistogramDatasetTransition = ({ width, height }) => {
  const url = useLocation().pathname === "/admin/total-income";
  const [data, setData] = useState();
  const data1 = useSelector((state) => state?.histogramReducer?.data);
  const data2 = useSelector((state) => state?.getUseageReducer?.data);
  useEffect(() => {
    if (url) {
      setData(data1);
    } else {
      setData(data2);
    }
  }, [data1, setData, data2, data]);
  return (
    <div>
      {data ? (
        url ? (
          <Histogram
            // width={width}
            height={height - BUTTONS_HEIGHT}
            data={data}
          />
        ) : (
          <HistogramUseage
            width={width}
            height={height - BUTTONS_HEIGHT}
            data={data}
          />
        )
      ) : (
        "loading"
      )}
    </div>
  );
};

const mapStateToProps = ({ histogram }) => {
  return { histogram };
};

export default connect(mapStateToProps, null)(HistogramDatasetTransition);
