import { Bootpay } from "@bootpay/client-js";
import { useEffect } from "react";
import { useDispatch } from "react-redux";
import { useLocation, useNavigate } from "react-router-dom";
import { getBootpay } from "../../modules/payment";

export default function Payment() {
  const dispatch = useDispatch();
  const location = useLocation();
  const navigation = useNavigate();

  const startPay = async (dispatch, location) => {
    try {
      const response = await Bootpay.requestPayment({
        application_id: "63d0816b3049c8001a5dc07b",
        price: location.state.depositeMoney,
        // price: "100",
        order_name: "",
        order_id: "TEST_ORDER_ID",
        pg: "kcp",
        method: "",
        tax_free: 0,
        user: {
          id: "",
          username: "",
          phone: "",
          email: "",
        },
        items: [
          {
            id: "item_id",
            name: "",
            qty: 1,
            price: location.state.depositeMoney,
            // price: 100,
          },
        ],
        extra: {
          open_type: "iframe",
          card_quota: "0,2,3",
          escrow: false,
        },
      });

      switch (response.event) {
        case "issued":
          //    
          break;
        case "done":
          console.log(response);
          dispatch(
            getBootpay({ DATA: response.data, kioskId: location.state.kioskId })
          );
          navigation("/bp/after/payment", {
            state: { kioskId: location.state.kioskId },
          });
          break;
        case "confirm": //payload.extra.separately_confirmed = true;       
          console.log(response.receipt_id);
          /**
           * 1.    
           * // validationQuantityFromServer(); //)      .
           */
          const confirmedData = await Bootpay.confirm(); // 
          if (confirmedData.event === "done") {
            // 
          } else if (confirmedData.event === "error") {
            //  
          }

          /**
           * 2.    
           * // requestServerConfirm(); //)       API .       .
           * Bootpay.destroy(); // .
           */
          break;
      }
    } catch (e) {
      //    
      // e.error_code -   
      // e.pg_error_code - PG  
      // e.message -  
      console.log(e.message);
      switch (e.event) {
        case "cancel":
          //    
          console.log(e.message);
          break;
        case "error":
          //      
          console.log(e.error_code);
          break;
      }
    }
  };

  useEffect(() => {
    if (!location.state) {
      navigation("/bp");
    } else {
      startPay(dispatch, location);
    }
  }, [navigation, dispatch, location, startPay]);

  return <></>;
}
